* {
  margin: 0;
}
.app{
  background-color: #fafafa;
}

.app__headerimage{
  
  width: 120px;
  height :50px;
  object-fit: contain;
  
}
.app__header{
  position: sticky;
  top: 0;
  z-index: 1;
  background-color: white;
  padding: 20px;
  object-fit: contain;
  border-bottom: 1px solid lightgray;
  display: flex;
  justify-content: space-between;
  
}
.app__signup {
  display: flex;
  flex-direction: column;
}
.app__posts {
  padding: 20px;
}